___CSS_LOADER_EXPORT___.push([module.id, `ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    max-width: 60%;
    background-color: var(--light-green);
    margin-left: 45px;
    min-height: 80%;
}

ul, li {
    border-radius: 7px;
}

li {
    padding: 20px;
    border-bottom: 2px  #9ec5ac solid;
    display: grid;
    grid-template: 1fr / 50px 3fr 1fr;
    column-gap: 20px;
    align-items: center;
}
li:hover {
    background-color: #c8f8d9;
}

li > input {
    width: 30px;
    height: 30px;
    grid-area: 1 / 1 / 2 / 2;
    align-self: center;
    justify-self: center;
    margin: 0;
}
.text {
    grid-area: 1 / 2 / 2 / 3;
}
.options {
    grid-area: 1 / 3 / 2 / 4;
    display: flex;
    justify-content: end;
}

.text p {
    margin: 0;
}
.text > .title {
    font-size: 25px;
    display: flex;
    align-items: center;
    gap: 5px;
}
.text > .due-date {
    font-size: 20px;
}
.options > img {
    width: 50px;
    height: 50px;
}

.title > img {
    width: 30px;
    height: 30px;
}`, "",{"version":3,"sources":["webpack://./src/objects/projectView.css"],"names":[],"mappings":"AAAA;IACI,qBAAqB;IACrB,aAAa;IACb,sBAAsB;IACtB,cAAc;IACd,oCAAoC;IACpC,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,iCAAiC;IACjC,aAAa;IACb,iCAAiC;IACjC,gBAAgB;IAChB,mBAAmB;AACvB;AACA;IACI,yBAAyB;AAC7B;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,wBAAwB;IACxB,kBAAkB;IAClB,oBAAoB;IACpB,SAAS;AACb;AACA;IACI,wBAAwB;AAC5B;AACA;IACI,wBAAwB;IACxB,aAAa;IACb,oBAAoB;AACxB;;AAEA;IACI,SAAS;AACb;AACA;IACI,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;AACA;IACI,eAAe;AACnB;AACA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB","sourcesContent":["ul {\n    list-style-type: none;\n    display: flex;\n    flex-direction: column;\n    max-width: 60%;\n    background-color: var(--light-green);\n    margin-left: 45px;\n    min-height: 80%;\n}\n\nul, li {\n    border-radius: 7px;\n}\n\nli {\n    padding: 20px;\n    border-bottom: 2px  #9ec5ac solid;\n    display: grid;\n    grid-template: 1fr / 50px 3fr 1fr;\n    column-gap: 20px;\n    align-items: center;\n}\nli:hover {\n    background-color: #c8f8d9;\n}\n\nli > input {\n    width: 30px;\n    height: 30px;\n    grid-area: 1 / 1 / 2 / 2;\n    align-self: center;\n    justify-self: center;\n    margin: 0;\n}\n.text {\n    grid-area: 1 / 2 / 2 / 3;\n}\n.options {\n    grid-area: 1 / 3 / 2 / 4;\n    display: flex;\n    justify-content: end;\n}\n\n.text p {\n    margin: 0;\n}\n.text > .title {\n    font-size: 25px;\n    display: flex;\n    align-items: center;\n    gap: 5px;\n}\n.text > .due-date {\n    font-size: 20px;\n}\n.options > img {\n    width: 50px;\n    height: 50px;\n}\n\n.title > img {\n    width: 30px;\n    height: 30px;\n}"],"sourceRoot":""}]);
