___CSS_LOADER_EXPORT___.push([module.id, `form {
    display: flex;
    flex-direction: column;
    padding-left: 45px;
    font-size: 20px;   
}
label, input[type="date"], select, button {
    width: fit-content;
}
input, select, button, textarea, label ~ div {
    margin-bottom: 30px;
    border-radius: 7px;
    border: 1px #c5c5c5 solid;
    padding: 5px;
}
input:focus, select:focus, textarea:focus {
    outline-color: var(--green);
}
label {
    font-weight: bold;
    margin-bottom: 5px;
}
input[type="text"] {
    max-width: 40%;
}
input[type="datetime-local"] {
    min-width: 250px;
    max-width: 20%;
}
textarea {
    max-width: 50%;
    min-height: fit-content;
}
select {
    background-color: white;
}
button {
    padding: 10px 30px;
    background-color: var(--green);
    color: white;
    border: none;
    font-weight: bold;
}
label ~ div {
    display: grid;
    grid-template: repeat(auto-fill, 1fr) / repeat(2, 1fr);
    width: fit-content;
    height: fit-content;
    column-gap: 20px;
    row-gap: 25px;
    padding: 10px;
}

label ~ div > * {
    margin: 0;
}
label ~ div > input {
    grid-column: 1 / 2;
}
label ~ div > label {
    grid-column: 2 / 3;
    font-weight: normal;
}`, "",{"version":3,"sources":["webpack://./src/actions/actions.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,eAAe;AACnB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,mBAAmB;IACnB,kBAAkB;IAClB,yBAAyB;IACzB,YAAY;AAChB;AACA;IACI,2BAA2B;AAC/B;AACA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,gBAAgB;IAChB,cAAc;AAClB;AACA;IACI,cAAc;IACd,uBAAuB;AAC3B;AACA;IACI,uBAAuB;AAC3B;AACA;IACI,kBAAkB;IAClB,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,sDAAsD;IACtD,kBAAkB;IAClB,mBAAmB;IACnB,gBAAgB;IAChB,aAAa;IACb,aAAa;AACjB;;AAEA;IACI,SAAS;AACb;AACA;IACI,kBAAkB;AACtB;AACA;IACI,kBAAkB;IAClB,mBAAmB;AACvB","sourcesContent":["form {\n    display: flex;\n    flex-direction: column;\n    padding-left: 45px;\n    font-size: 20px;   \n}\nlabel, input[type=\"date\"], select, button {\n    width: fit-content;\n}\ninput, select, button, textarea, label ~ div {\n    margin-bottom: 30px;\n    border-radius: 7px;\n    border: 1px #c5c5c5 solid;\n    padding: 5px;\n}\ninput:focus, select:focus, textarea:focus {\n    outline-color: var(--green);\n}\nlabel {\n    font-weight: bold;\n    margin-bottom: 5px;\n}\ninput[type=\"text\"] {\n    max-width: 40%;\n}\ninput[type=\"datetime-local\"] {\n    min-width: 250px;\n    max-width: 20%;\n}\ntextarea {\n    max-width: 50%;\n    min-height: fit-content;\n}\nselect {\n    background-color: white;\n}\nbutton {\n    padding: 10px 30px;\n    background-color: var(--green);\n    color: white;\n    border: none;\n    font-weight: bold;\n}\nlabel ~ div {\n    display: grid;\n    grid-template: repeat(auto-fill, 1fr) / repeat(2, 1fr);\n    width: fit-content;\n    height: fit-content;\n    column-gap: 20px;\n    row-gap: 25px;\n    padding: 10px;\n}\n\nlabel ~ div > * {\n    margin: 0;\n}\nlabel ~ div > input {\n    grid-column: 1 / 2;\n}\nlabel ~ div > label {\n    grid-column: 2 / 3;\n    font-weight: normal;\n}"],"sourceRoot":""}]);
