    font-family: roboto;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
:root {
    --green: #30be62;
    --light-green: #dfffea; 
    --hover-green: #c8f8d9;
}
body {
    display: grid;
    grid-template-columns: 1fr 3fr;
    min-height: 100vh;
    max-width: 100vw;
    font-family: roboto, sans-serif;
}

aside, main {
    display: flex;
    flex-direction: column;
}
aside {
    background-color: var(--light-green);
    padding: 15px;
    gap: 15px;
}

section {
    padding: 15px;
}

section > * {
    border-radius: 15px;
}
section > div {
    display: flex;
    gap: 10px;
    align-items: center;
    padding: 10px 10px;
}
.action, .project {
    margin-left: 20px;
    font-size: 20px;
}
.action:hover, .project:hover {
    background-color: var(--hover-green);
}
.project {
    display: grid;
    grid-template-columns: 30px 1fr repeat(2, 30px);
}

section > div > * {
    margin: 0;
}
section > div > img {
    width: 30px;
    height: 30px;
}

main {
    padding: 45px;
}

main > * {
    margin: 0;
    padding: 20px;
}`, "",{"version":3,"sources":["webpack://./src/index.css"],"names":[],"mappings":"AAAA;IACI,mBAAmB;IACnB,4CAAyB;AAC7B;AACA;IACI,gBAAgB;IAChB,sBAAsB;IACtB,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,8BAA8B;IAC9B,iBAAiB;IACjB,gBAAgB;IAChB,+BAA+B;AACnC;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;AACA;IACI,oCAAoC;IACpC,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,kBAAkB;AACtB;AACA;IACI,iBAAiB;IACjB,eAAe;AACnB;AACA;IACI,oCAAoC;AACxC;AACA;IACI,aAAa;IACb,+CAA+C;AACnD;;AAEA;IACI,SAAS;AACb;AACA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,SAAS;IACT,aAAa;AACjB","sourcesContent":["@font-face {\n    font-family: roboto;\n    src: url(../res/font.ttf);\n}\n:root {\n    --green: #30be62;\n    --light-green: #dfffea; \n    --hover-green: #c8f8d9;\n}\nbody {\n    display: grid;\n    grid-template-columns: 1fr 3fr;\n    min-height: 100vh;\n    max-width: 100vw;\n    font-family: roboto, sans-serif;\n}\n\naside, main {\n    display: flex;\n    flex-direction: column;\n}\naside {\n    background-color: var(--light-green);\n    padding: 15px;\n    gap: 15px;\n}\n\nsection {\n    padding: 15px;\n}\n\nsection > * {\n    border-radius: 15px;\n}\nsection > div {\n    display: flex;\n    gap: 10px;\n    align-items: center;\n    padding: 10px 10px;\n}\n.action, .project {\n    margin-left: 20px;\n    font-size: 20px;\n}\n.action:hover, .project:hover {\n    background-color: var(--hover-green);\n}\n.project {\n    display: grid;\n    grid-template-columns: 30px 1fr repeat(2, 30px);\n}\n\nsection > div > * {\n    margin: 0;\n}\nsection > div > img {\n    width: 30px;\n    height: 30px;\n}\n\nmain {\n    padding: 45px;\n}\n\nmain > * {\n    margin: 0;\n    padding: 20px;\n}"],"sourceRoot":""}]);
